<template>
<div class="" style="position: relative;">
	<img class="qr-img" src="../../assets/shops/imgs/vip-code.png"/>
	<div class="user">
		<img src="../../assets/shops/imgs/img-gift.png"/><span>张三</span>
	</div>
	<div class="qr-code"><img :src="src"/></div>
</div>
</template>

<script>
export default{
	data(){
		return{
			src:''
		}
	},
	mounted(){
		this.post('home/User/qr',{}).then(res=>{})
		console.log(res)
	}
}
</script>

<style scoped>
.qr-img{width: 7.5rem;height: 12.06rem;}
.user{position: absolute;top: 4.1rem;left: 3.1rem;color: #fff;}
.user img{width: .8rem;height: .8rem;border-radius: 50%;margin-right: .2rem;}
.qr-code{position: absolute;top: 5.4rem;left: 2.4rem;}
.qr-code img{width: 2.7rem;height: 2.7rem;}
/*.person {
    width: 100%;
    position: relative;
    
   background: url(../../assets/shops/imgs/vip-code.png)no-repeat;
    background-size: contain;
    overflow-y: auto;
  }*/
</style>


